/* contact page css*/
.contact-body{
  margin: auto;
  position: fixed;
  width: 100%;
  height: 100%;
  justify-content: center;
  padding-top: 2em; 
}
.contact-grid{
  text-align: center;
  justify-content: center;
  width:80%;
  height: 550px;
  background:white;
  color: black;
  margin-bottom: 20px;
}

.contact-grid h2{
  font-family: 'Anton',sans-serif;
}

.contact-grid p{
  font-family: 'Oxygen',sans-serif;
  font-weight: bold;
}
.me {
  width:150px;
  height: 150px;
  border-radius:30%;
  overflow:hidden;
}
.profile{
  width:100%;
  height: 100%;
  object-fit: cover;
}
.contact-grid hr{
  border-top:5px dotted black;
  width:50%;
  margin:auto;
}

.contact-list i{
  font-size:66px;
  padding-right: 5rem;
}
/* contact page css*/

/* Resume page css*/
.resume-right-col{
  background: #27221F;
  color: white;
}



/* Resume page css*/

.avatar-img{
  height: 250px;
  padding-top:5em;
}

.banner-text {
  background-color: black;
  opacity: .8;
  width: 75%;
  margin: auto;
  border-radius: 10px;
}

.banner-text h1 {
  font-size: 66px;
  font-weight: bold;
  color: white;
}

.banner-text hr {
  border-top: 5px dotted white;
  width: 50%;
  margin: auto;
}

.banner-text p {
  color: white;
  font-size: 20px;
  padding: 1em;
}

.social-links {
  display: flex;
  justify-content: space-between;
  width: 50%;
  margin: auto;
}

.social-links i {
  color: white;
  font-size: 5em;
  padding: 8px;
}